import React from 'react';
import CardEvent from '../../components/CardEvent';

import Footer from './components/Footer';

const Laboratorio = () => {
  return (
    <CardEvent
      date="01/02/2019 - 25/03/2019"
      header={{ text: 'Laboratorio de análisis clínicos - V2' }}
      body={<p>Creación y actualización de módulos</p>}
      footer={<Footer />}
    />
  );
};

export default Laboratorio;
