import React from 'react';
import CardEvent from '../../components/CardEvent';

import header from './components/Header';
import GenetateIcons from '../../../Generate/Icons';
import {
  bootstrapImg,
  cssImg,
  htmlImg,
  javascriptImg,
  mysqlImg,
  phpImg,
  vueImg,
} from '../../../Icons';

const PagosUniversidad = () => {
  const icons = [phpImg, javascriptImg, cssImg, htmlImg, bootstrapImg, vueImg, mysqlImg];
  return (
    <CardEvent
      date="01/02/2019 - 25/03/2019"
      header={header}
      body={
        <p>
          Desarrollo de sistema que gestiona de usuarios pagos, notificaciones mediante
          correos, y control de pagos de los alumnos para la universidad Filadelfia de méxico.
        </p>
      }
      footer={
        <>
          <div className="links">
            <a
              href="https://universidadfiladelfia.edu.mx/"
              target="_blank"
              rel="noopener noreferrer">
              Página web
            </a>
          </div>
          <div className="icons">
            <GenetateIcons icons={icons} />
          </div>
        </>
      }
    />
  );
};

export default PagosUniversidad;
