import React from 'react';
import CardEvent from '../../components/CardEvent';
import Footer from './components/Footer';
import Header from './components/Header';

import GenerateListLinks from '../../../Generate/ListLinks';

const Actual = () => {
  const hoy = () => {
    const without0 = (val) => (String(val).length === 1 ? `0${val}` : val);

    const fecha = new Date();
    const dia = fecha.getDate();
    const mes = fecha.getMonth() + 1;
    const anio = fecha.getFullYear();
    const res = `${without0(dia)}/${without0(mes)}/${anio}`;
    return res;
  };

  const pages = [
    { link: '/', title: 'Esta pagina', local: true },
    { link: 'https://beatlion.github.io/gradient/', title: 'Generador de gradiente (básico)' },
    { link: 'https://iwannasoftware.web.app/', title: 'Iwannasoftware' },
    // { link: '/#', title: 'Esta pagina' },
  ];

  return (
    <CardEvent
      date={hoy()}
      header={Header}
      body={
        <>
          <p>Estudios especializados en el diseño, desarrollo e implementación de software</p>
          <GenerateListLinks title="Proyectos Realizados" pages={pages} />
        </>
      }
      footer={<Footer />}
    />
  );
};

export default Actual;
