import React from 'react';
import {
  htmlImg,
  laravelImg,
  cssImg,
  tailwindImg,
  reactImg,
  vueImg,
  nodejsImg,
} from '../../../../Icons';

import GenerateIcons from '../../../../Generate/Icons';

const Footer = () => {
  const icons = [htmlImg, cssImg, reactImg, vueImg, tailwindImg, laravelImg, nodejsImg];

  return (
    <div className="icons">
      <GenerateIcons icons={icons} />
    </div>
  );
};

export default Footer;
