import React from 'react';
import { androidImg, arduinoImg, javaImg, htmlImg, bootstrapImg } from '../../../../Icons';

import GenerateIcons from '../../../../Generate/Icons';

const Footer = () => {
  const icons = [javaImg, htmlImg, arduinoImg, bootstrapImg, androidImg];

  return (
    <>
      <div className="links">
        <a
          href="http://uptapachula.edu.mx/page/"
          target="_blank"
          rel="noreferrer"
          alt="#uptap">
          Página web
        </a>
      </div>

      <div className="icons">
        <GenerateIcons icons={icons} />
      </div>
    </>
  );
};

export default Footer;
