import React from 'react';
import PropTypes from 'prop-types';

import { arrowImg } from '../../Icons';

const Links = ({ pages, title }) => {
  return (
    <ul className="pt-2">
      <li className="font-semibold">{title}</li>
      {pages.map((item, index) => (
        <li key={index}>
          {item.local ? (
            <a href={item.link} className="pl-2">
              <img src={arrowImg} alt="" className="inline w-4 h-3" /> {item.title}
            </a>
          ) : (
            <a href={item.link} target="_blank" rel="noreferrer" className="pl-2">
              <img src={arrowImg} alt="" className="inline w-4 h-3" /> {item.title}
            </a>
          )}
        </li>
      ))}
    </ul>
  );
};

Links.propTypes = {
  title: PropTypes.string.isRequired,
  pages: PropTypes.arrayOf(
    PropTypes.shape({
      link: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      local: PropTypes.bool,
    })
  ),
};

Links.defaultProps = {
  title: '',
  pages: [],
  local: false,
};

export default Links;
