import './styles.scss';
import { FaHandPointUp } from 'react-icons/fa';

import { useDarkTheme } from '../../context/DarkTheme';

export default function UpPage() {
  const { scrollY } = useDarkTheme();

  const goTop = () => {
    window.scroll({
      top: 0,
      behavior: 'smooth',
    });
  };

  const showClass = scrollY > 500 ? 'translate-y-0' : 'translate-y-28';

  return (
    <div className={`up__page__container transform ${showClass}`} onClick={goTop}>
      <FaHandPointUp className="up__page__icon " />
    </div>
  );
}
