import React from 'react';
import CardEvent from '../../components/CardEvent';

import Footer from './components/Footer';

const Laboratorio = () => {
  return (
    <CardEvent
      date="15/06/2018 - 10/09/2018"
      header={{ text: 'Laboratorio de análisis clínicos - V1' }}
      body={
        <p>
          Desarrollo de sistema para el manejo de usuarios, pacientes, análisis, resultados de
          análisis, gráficos del comportamiento de los análisis mas comunes, para laboratorio
          privado
        </p>
      }
      footer={<Footer />}
    />
  );
};

export default Laboratorio;
