import React from 'react';
import PropTypes from 'prop-types';

const Icons = ({ icons }) => {
  return (
    <>
      {icons.map((item, index) => (
        <img src={item} key={index} alt={index} />
      ))}
    </>
  );
};

Icons.propTypes = {
  icons: PropTypes.array.isRequired,
};

export default Icons;
