import React from 'react';
import CardEvent from '../../components/CardEvent';

import header from './components/Header';
import Footer from './components/Footer';

const Universidad = () => {
  return (
    <CardEvent
      date="2012 - 2016"
      header={header}
      body={
        <p>
          Estudios basados en competencias especializados en el diseño, desarrollo,
          actualización e implementación de software. Modelo de estancias y estadías
          profesionales, para un desarrollo mas competente en el area.
        </p>
      }
      footer={<Footer />}
    />
  );
};

export default Universidad;
