import './styles.scss';
import React from 'react';
import PropTypes from 'prop-types';

import { TextEvent } from '@merc/react-timeline';

const Content = ({ children }) => <div className="card__event__container">{children}</div>;

const CardEvent = ({ date, header, body, footer }) => {
  return (
    <TextEvent date={date} text="" card={Content}>
      <div className="card__event__header ">
        <div className="card__event__header__date">
          <span>{date}</span>
        </div>

        <div className="card__event__header__content">
          {header.image && (
            <img
              src={header.image}
              alt={header.altImage}
              className={`card__event__logo ${
                header.classImage ? header.classImage : 'bg-blueGray-300'
              }`}
            />
          )}

          {header.text && <div className="card__event__title">{header.text}</div>}
        </div>
      </div>

      {body && <div className="card__event__body">{body}</div>}

      {footer && <div className="card__event__footer">{footer}</div>}
    </TextEvent>
  );
};

CardEvent.propTypes = {
  date: PropTypes.string.isRequired,
  body: PropTypes.element,
  footer: PropTypes.element,
  header: PropTypes.shape({
    text: PropTypes.string.isRequired,
    image: PropTypes.string,
    altImage: PropTypes.string,
    classImage: PropTypes.string,
  }),
};

CardEvent.defaultProps = {
  body: null,
  footer: null,
  header: {
    text: '',
    image: null,
    altImage: null,
    classImage: null,
  },
};

export default CardEvent;
