import React from 'react';
import CardEvent from '../../components/CardEvent';

import Footer from './components/Footer';
import header from './components/Header';
import Proyectos from './components/Proyectos';

const EnnovasoftInicio = () => {
  return (
    <CardEvent
      date="16/02/2018 - 26/03/2020"
      header={header}
      body={
        <>
          <p>
            Inicio en el puesto de Desarrollador Full stack, enfocado en desarrollo web, en mis
            roles esta la creación de paginas estáticas y aplicaciones web
          </p>
          <Proyectos />
        </>
      }
      footer={<Footer />}
    />
  );
};

export default EnnovasoftInicio;
