import React from 'react';
import {
  phpImg,
  javascriptImg,
  cssImg,
  htmlImg,
  bootstrapImg,
  vueImg,
  mysqlImg,
} from '../../../../Icons';

import GenerateIcons from '../../../../Generate/Icons';

const Footer = () => {
  const icons = [phpImg, javascriptImg, cssImg, htmlImg, bootstrapImg, vueImg, mysqlImg];

  return (
    <div className="icons">
      <GenerateIcons icons={icons} />
    </div>
  );
};

export default Footer;
