import React from 'react';
import CardEvent from '../../components/CardEvent';

import header from './components/Header';
import Footer from './components/Footer';

const BlueRadio = () => {
  return (
    <CardEvent
      date="2012 - 2016"
      header={header}
      body={
        <p>
          Se creo la página principal de la radio, al igual que el administrador de contenido
          (programación, imágenes, videos de youtube)
        </p>
      }
      footer={<Footer />}
    />
  );
};

export default BlueRadio;
