import React from 'react';

import {
  phpImg,
  javascriptImg,
  cssImg,
  htmlImg,
  bootstrapImg,
  prestashopImg,
  sassImg,
} from '../../../../Icons';

import GenerateIcons from '../../../../Generate/Icons';

const Footer = () => {
  const icons = [javascriptImg, phpImg, cssImg, htmlImg, bootstrapImg, sassImg, prestashopImg];
  return (
    <>
      <div className="links">
        <a href="https://oxium.mx/" target="_blank" rel="noopener noreferrer">
          Página web
        </a>
      </div>
      <div className="icons">
        <GenerateIcons icons={icons} />
      </div>
    </>
  );
};

export default Footer;
