import GenerateIcons from '../../../../Generate/Icons';

const Footer = () => {
  const icons = [javaImg, javascriptImg, cssImg, htmlImg, mysqlImg, bootstrapImg, springImg];
  return (
    <>
      {/* <div className="links">
        <a href="http://ennovasoft.com/" target="_blank" rel="noopener noreferrer">
          Página web
        </a>
      </div> */}

      <div className="icons">
        <GenerateIcons icons={icons} />
      </div>
    </>
  );
};

export default Footer;
