import React from 'react';
import CardEvent from '../../components/CardEvent';

import header from './components/Header';
import Footer from './components/Footer';

import GenerateListLinks from '../../../Generate/ListLinks';

const Freelance = () => {
  const pages = [
    { link: 'https://sofa55.com.mx/', title: 'Fofa 55' },
    {
      link: 'http://fixit-services.com/',
      title: 'Fixit services',
    },
  ];

  return (
    <CardEvent
      date="05/05/2020 - 28/12/2020"
      header={header}
      body={
        <>
          <p>Desarrollador freelance, desarrollo de paginas web estáticas, y tienda online</p>
          <GenerateListLinks title="Proyectos realizados" pages={pages} />
        </>
      }
      footer={<Footer />}
    />
  );
};

export default Freelance;
