import React from 'react';
import { htmlImg, bootstrapImg, javascriptImg, cssImg, phpImg } from '../../../../Icons';

import GenerateIcons from '../../../../Generate/Icons';

const Footer = () => {
  const icons = [javascriptImg, phpImg, cssImg, htmlImg, bootstrapImg];

  return (
    <>
      <div className="links">
        <a href="http://blueradio.com.mx/" target="_blank" rel="noreferrer" alt="blue radio">
          Página web
        </a>
      </div>

      <div className="icons">
        <GenerateIcons icons={icons} />
      </div>
    </>
  );
};

export default Footer;
